
import Toast from 'vux/src/components/toast/index.vue'


import serviceList from '../../components/ServiceList';
import tabBar from '../../components/TabBar';
import tools from '../../lib/tools.js';
import healthy from '../../lib/healthy.js';
export default {
name:'healthCenter',
components:{tabBar,Toast,serviceList},
data(){
return {
id:'',
info:{},
record:{},
recommendServices:[],
toastMsg:'',
showToast:false
}
},
mounted(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getCookie('user_id'));//md === userid

if(!id || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.id = id;
this.$http.post('/apiUser/queryAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
this.getNumberMouth();
this.getServiceByKeywords('1');
this.getServiceByKeywords('2');
},
methods:{
getStatus(obj){
switch (obj.dataType){
case "HR":
return healthy.heartRateReport(obj.value).status;
case "BO":
return healthy.bloodOxygenReport(obj.value).status;
case "BMI":
return healthy.weightHandle(obj.heValue,obj.weValue).status;
case "BP":
return healthy.bloodPressureReport(obj.sys,obj.dia).status;
case "BG":
return healthy.bloodSugarReport(obj.value).status;
case "UA":
return healthy.uaReport(obj.value).status;
case "TE":
return healthy.tempReport(obj.value).status;
case "WP":
return healthy.wpReport(obj.value).status;
case "BONE":
return healthy.boneReport(obj.value).status;
case "BMR":
return healthy.bmrReport(obj.value).status;
case "CHOL":
return healthy.cholReport(obj.value).status;
case "BHB":
return healthy.hbReport(obj.value).status;
case "MUCALE":
return healthy.muscleReport(obj.weight,obj.value).status;
case "PIW":
return healthy.piwReport(obj.weight,obj.value).status;
case "FP":
return healthy.fpReport(obj.value).status;
case "BF":
return healthy.cholReport(obj.value).status;
default:
break;
}
},
getNumberMouth(){
let vm = this;
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id'));

this.$http.axios({method:'get',url:'/deviceData/getMeasureData?managerId='+managerId+'&time='}).then((result) => {
result = result.data;
if(result.successful && (result.status==200)){
let tempRecord = {};
for(let i=0;i<result.data.length;i++){
let item = result.data[i];
tempRecord[item.dataType] = item.value + item.units;
if(item.dataType=='BMI'){
tempRecord['TZTZ'] = item.weValue + 'KG';
}
}
vm.record = tempRecord;
console.log(vm.record);
}else{
vm.record = [];
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
console.log(result);
}
},(error) => {
console.log(error);
});
},
goTo(type){
if(type=='healthRecords'){
tools.next('/m/healthRecords.html');
}else if(type=='personalInfo'){
tools.next('/m/personalInfo.html');
}
},
getServiceByKeywords(key){
var vm = this;
this.$http.post('/service/findServiceByKeywords',{currentPage:1,showCount:20,keywordsId:key,type:1}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.recommendServices = [...vm.recommendServices,...data.items];
}else{
this.showToast = true;
this.toastMsg = '查询失败';
console.log(data);
}
},(error) => {
console.log(error);
});
this.toastMsg = '';
}
}
}



