      // 体重
      /*
       计算体重
       入参：hi =身高（cm）, wi = 体重（kg）
       返回值：
       backData.bmi        //BMI
       backData.loWeight   //最低体重
       backData.hiWeight   //最高体重
       backData.status     //描述
       backData.report	    //结论
       */
      weightHandle: function weightHandle(hi, wi) {
            //转换初始值
            var heigh = (parseFloat(hi) / 100).toFixed(2); //身高转换为 M
            var weight = parseFloat(wi); //体重转化（KG）
            //处理数据
            var bmiDa = this.computeBMI(heigh, weight); //计算BMI
            var standerDa = this.standardWeight(heigh); //计算标准体重
            var reportDa = this.weightReport(bmiDa); //计算报告
            //返回结果
            var backData = new Object();
            backData.bmi = bmiDa; //BMI
            backData.loWeight = standerDa.loWeight; //最低体重
            backData.hiWeight = standerDa.hiWeight; //最高体重
            backData.status = reportDa.status; //描述
            backData.report = reportDa.report; //结论
            return backData;

            /*
             var showString = "";
             showString += "BMI="+bmiDa+"\n\r";
             showString += "loWeight="+standerDa.loWeight+", hiWeight="+standerDa.hiWeight+"\n\r";
             showString += "report="+reportDa.report+", status="+reportDa.status +"\n\r";
               alert(showString);
             */
      },

      //计算BMI  hig：身高， wei：体重  【 BMI =  体重(KG) / 身高（M） * 身高（M）】
      computeBMI: function computeBMI(hig, wei) {
            //转化入参
            var dataBMI = (wei / (hig * hig)).toFixed(2);
            return dataBMI;
      },

      //计算标准体重范围方法  【体重(KG) = BMI * 身高（M） * 身高（M）】
      standardWeight: function standardWeight(heigh) {
            // var heigh = (parseFloat(hig)/100).toFixed(2);  //身高转换为M
            //定义区间BMI
            var loBMI = 18.5;
            var hiBMI = 24;
            //最低体重
            var lo = (loBMI * heigh * heigh).toFixed(2); //最低身高
            var hi = (hiBMI * heigh * heigh).toFixed(2); //最高身高
            //返回参数
            var back = new Object();
            back.loWeight = lo;
            back.hiWeight = hi;
            return back;
      },

      //根据BMI判断推荐报告
      weightReport: function weightReport(val) {
            // var value = parseFloat(val);
            var value = val;
            //返回结果
            var backString = "";
            var status = "";
            //比较值
            var count1 = 18.5;
            var count2 = 24;
            var count3 = 27;
            //比较结果
            if (value < count1) {
                  backString = "偏瘦，相关疾病发病的危险性低，但其它疾病危险性增加";
                  status = "偏瘦";
            } else if (count1 <= value && value < count2) {
                  backString = "正常，相关疾病发病的危险性正常";
                  status = "正常";
            } else if (count2 <= value && value <= count3) {
                  backString = "超重偏胖，相关疾病发病的危险性增加";
                  status = "超重偏胖";
            } else if (value > count3) {
                  backString = "肥胖，相关疾病发病的危险性严重增加";
                  status = "肥胖";
            }
            //返回参数
            var backObject = new Object();
            backObject.report = backString;
            backObject.status = status;
            return backObject;
      },

